export default {
	data() {
		return {
			dotsStyles: {
				backgroundColor: 'rgba(249, 249, 251, 1)',
				border: 'none',
				color: '#fff',
				selectedBackgroundColor: 'rgba(3, 190, 144, 1)',
				selectedBorder: 'none'
			},
			mode: 'dot',
			current:0,
			tabBars:[],
			bannerList: [],
			hotProductList:[],
			newProductList:[],
			communityId:''
		};
	},
	onLoad({communityId}) {
		this.communityId = '292138634851640796' //communityId
		this.requestData();
		this.getHotProduct();
		this.getBannerList();
		this.getNewProduct()
	},
	methods: {
		search(res) {
			uni.navigateTo({
				url: `/pages/health-search-page/health-search-page?value=${res.value}`,
			});
		},
		change(e) {
			this.current = e.detail.current
		},
		ontabtap(e) {
		    let index = e.target.dataset.current || e.currentTarget.dataset.current;
		},
		goDetail(id,type){
			uni.navigateTo({
				url: `/pages/health-product-detail/health-product-detail?id=${id}&type=${type}&communityId=${this.communityId}`,
			});
		},
		requestData(){
			this.$api.productClassifyList().then(res=>{
				if(res.status=="OK"){
					let arr = res.data
					arr.map(item=>{
						this.tabBars.push({
							id:item.id,
							label:item.name,
							value:''
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		getHotProduct(){
			this.$api.communityProductList({
				size:999,
				page:1,
				hot:true,
			}).then(res=>{
				console.log(res);
				if(res.status=="OK"){
					res.list.map(item=>{
						this.hotProductList.push({
							price:item.price/100,
							originalPrice:item.originalPrice/100,
							name:item.name,
							pic:JSON.parse(item.icon)[0].url,
							id:item.id
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		getNewProduct(){
			this.$api.communityProductList({
				size:999,
				page:1,
				recommend:true,
			}).then(res=>{
				console.log(res);
				if(res.status=="OK"){
					res.list.map(item=>{
						this.newProductList.push({
							price:item.price/100,
							originalPrice:item.originalPrice/100,
							name:item.name,
							pic:JSON.parse(item.icon)[0].url,
							id:item.id
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		},
		getBannerList(){
			this.$api.communityProductList({
				size:999,
				page:1,
				banner:true,
			}).then(res=>{
				console.log(res);
				if(res.status=="OK"){
					res.list.map(item=>{
						this.bannerList.push({
							url:JSON.parse(item.bannerPic)[0].url,
							name:item.name,
							id:item.id
						})
					})
				}
			}).catch(err=>{
				console.log(err);
			})
		}
	},
	filters: {
		toFixed2: function(value) {
			return value.toFixed(2);
		},
	}
}
